section#login {
    article.login--container {
        @include centered();
        width: 100%;
        max-width: 400px;
        padding: 0 1rem;

        h4 {
            text-align: center;
            font-weight: bold;
            margin-bottom: 3rem;
        }

        form {
            display: flex;
            flex-direction: column;
        }

        nav {
            display: flex;
            justify-content: space-between;
            margin-bottom: 1.5rem;

            ul {
                display: flex;
                align-items: flex-end;

                li {
                    font-size: 12px;
                    margin-right: 0.5rem;
                    padding-right: 0.5rem;
                    border-right: 1px solid rgba(0, 0, 0, 0.1);

                    &:last-child {
                        border: 0;
                        margin-right: 0;
                        padding-right: 0;
                    }
                }
            }
        }

        p {
            font-size: 12px;
            a {
                font-size: 13px;
                color: rgba(128, 211, 142, 1);
                margin-left: 1rem;
            }
        }
    }
}
