footer#root-footer {
    // min-height: 300px;
    background-color: #fafafa;

    .container {
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;
        padding: 1.5rem;

        @media (min-width: 768px) {
            padding: 2rem;
            padding-bottom: 0.5rem;
            display: flex;
        }

        @media (min-width: 1680px) {
            padding: 2rem 0;
        }

        .wrapper--one,
        .wrapper--two {
            @media (min-width: 768px) {
                width: 50%;
            }
        }

        .wrapper--one {
            ul.customer--pages {
                display: flex;
                margin-bottom: 1.5rem;

                li {
                    margin-right: 1.5rem;
                    font-size: 12px;

                    @media (min-width: 768px) {
                        font-size: 14px;
                    }
                }
            }

            .social {
                display: flex;
                margin-bottom: 1.5rem;

                figure {
                    &:first-child {
                        margin-right: 1.7rem;
                    }

                    &.instagram,
                    &.facebook {
                        img {
                            width: 26px;
                        }
                    }
                }
            }
        }

        .wrapper--two {
            @media (min-width: 768px) {
                padding-left: 2rem;
            }
            @media (min-width: 1024px) {
                padding-left: 5rem;
            }
            .company--info {
                font-size: 9px;
                color: rgba(62, 62, 62, 0.53);

                @media (min-width: 768px) {
                    font-size: 11px;
                }
                @media (min-width: 1024px) {
                    font-size: 12px;
                }

                li {
                    margin-bottom: 0.7rem;

                    &:last-child {
                        img {
                            width: 18px;
                            opacity: 0.3;
                        }
                    }
                }
            }
        }
    }

    .line {
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;
        padding-bottom: 5rem;
        border-top: 1px solid rgba(0, 0, 0, 0.1);
        display: none;

        @media (min-width: 768px) {
            display: block;
        }
    }
}
