label.inputbox--component {
    position: relative;
    width: 100%;
    margin-bottom: 0.5rem;

    &.error {
        margin-bottom: 1rem;
        span.warning {
            visibility: visible;
        }
    }

    &.avail {
        margin-bottom: 1rem;
        span.able {
            visibility: visible;
        }
    }

    input {
        width: 100%;
        height: 36px;
        border: 1px solid rgba(0, 0, 0, 0.1);
        padding: 0 0.8rem;

        @media (min-width: 768px) {
            height: 44px;
        }
    }

    span {
        position: absolute;
        bottom: -12px;
        left: 0;
        font-size: 10px;
        visibility: hidden;

        @media (min-width: 768px) {
            bottom: -13px;
            font-size: 11px;
        }

        &.warning {
            color: rgb(190, 23, 23);
        }

        &.able {
            color: rgb(23, 81, 190);
        }
    }
}
