article.suggest--container {
    background-color: white;
    padding-top: 6rem;

    @media (min-width: 768px) {
        padding-top: 10rem;
    }

    .wrap {
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;

        hgroup.title {
            text-align: center;
            margin-bottom: 3rem;

            @media (min-width: 768px) {
                margin-bottom: 5rem;
            }

            h3 {
                font-size: 20px;
                font-weight: bold;

                @media (min-width: 768px) {
                    font-size: 35px;
                }
            }
        }

        .products--wrap {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            // padding: 0 1rem;

            @media (min-width: 768px) {
                padding: 0 1.8rem;
                justify-content: space-between;
            }
            @media (min-width: 1024px) {
                padding: 0 2rem;
            }
            @media (min-width: 1680px) {
                padding: 0;
            }
        }

        .more {
            @include flexCentered();
            padding: 0 0 3rem;

            @media (min-width: 768px) {
                padding: 1rem 0 3rem;
            }

            a {
                @include flexCentered();
                width: 116px;
                height: 29px;
                border: 1px solid rgba(0, 0, 0, 0.1);
                font-size: 13px;

                @media (min-width: 768px) {
                    width: 168px;
                    height: 33px;
                    font-size: 17px;
                }
            }
        }
    }
}
