.itemcard--wrap {
    width: 100%;
    margin-bottom: 3rem;
    max-width: 257px;

    @media (min-width: 1024px) and (max-width: 1365px) {
        &:last-child {
            display: none;
        }
    }

    @media (min-width: 768px) {
        width: 50%;
        max-width: 340px;
    }

    @media (min-width: 1024px) {
        width: 32%;
        max-width: 310px;
    }

    @media (min-width: 1366px) {
        width: 24%;
        max-width: 340px;
    }

    figure {
        display: flex;
        flex-direction: column;
        overflow: hidden;
        width: 100%;
        // max-width: 257px;
        margin: 0 auto;

        // @media (min-width: 768px) {
        //     max-width: 340px;
        // }
        // @media (min-width: 1366px) {
        //     max-width: 310px;
        // }
        // @media (min-width: 1440px) {
        //     max-width: 340px;
        // }

        img {
            object-fit: cover;
            max-height: 333px;

            @media (min-width: 768px) {
                max-height: 440px;
            }
            @media (min-width: 1366px) {
                max-height: 420px;
            }
            @media (min-width: 1440px) {
                max-height: 440px;
            }
        }

        figcaption {
            padding-top: 0.5rem;
            font-size: 15px;
            line-height: 1.8;

            p.price {
                font-weight: bold;
            }

            @media (min-width: 768px) {
                font-size: 18px;

                p.price {
                    font-size: 16px;
                }
            }
        }
    }
}
