section#basic--main {
    position: relative;
    padding-top: 100vh;
    margin-top: -60px;

    @media (min-width: 768px) {
        padding-top: 400px;
        margin-top: -110px;
    }

    @media (min-width: 1366px) {
        padding-top: 723.83px;
    }

    figure.intro--image-container {
        position: fixed;
        top: 0;
        z-index: -1;
        width: 100%;
        height: 100vh;
        overflow: hidden;

        @media (min-width: 768px) {
            height: 400px;
        }
        @media (min-width: 1366px) {
            height: 723.83px;
        }

        img {
            width: 100%;
            height: 100vh;
            object-fit: cover;
        }

        .comment--wrap {
            position: absolute;
            top: 35%;
            left: 6%;

            @media (min-width: 768px) {
                top: 35%;
                left: 10%;
            }
            @media (min-width: 1366px) {
                top: 40%;
                left: 10%;
            }
            @media (min-width: 1680px) {
                top: 40%;
                left: 15%;
            }
            @media (min-width: 1920px) {
                top: 40%;
                left: 20%;
            }
            @media (min-width: 2560px) {
                top: 40%;
                left: 25%;
            }

            p {
                color: white;
                line-height: 1.1;
                font-size: 31px;

                @media (min-width: 768px) {
                    font-size: 51px;
                }
                @media (min-width: 1366px) {
                    font-size: 81px;
                }

                span {
                    font-size: 26px;
                    font-weight: lighter;

                    @media (min-width: 768px) {
                        font-size: 43px;
                    }
                    @media (min-width: 1366px) {
                        font-size: 66px;
                    }
                }
            }
        }
    }

    nav.menu--container-desk {
        display: none;
        background-color: white;
        transition: 0.3s;

        @media (min-width: 768px) {
            display: block;

            ul {
                width: 100%;
                max-width: $defaultMaxWidth;
                height: 100px;
                margin: 0 auto;
                display: flex;

                @media (min-width: 1440px) {
                    height: 150px;
                }

                li {
                    position: relative;
                    flex: 1;
                    height: 100%;

                    figure {
                        @include centered();
                        width: 80px;
                        height: 65px;
                        font-size: 14px;

                        display: flex;
                        flex-direction: column;
                        align-items: center;

                        @media (min-width: 1440px) {
                            width: 80px;
                            height: 100px;
                            font-size: 15px;
                        }

                        img {
                            height: 60%;
                        }

                        figcaption {
                            white-space: nowrap;
                        }
                    }

                    .line {
                        position: absolute;
                        top: 50%;
                        right: 0;
                        transform: translateY(-50%);
                        height: 30%;
                        border-right: 1px solid rgba(0, 0, 0, 0.2);
                    }

                    &:last-child {
                        .line {
                            display: none;
                        }
                    }
                }
            }
        }
    }

    .banner--top-container {
        position: relative;
        overflow: hidden;
        background-color: rgba(62, 62, 62, 1);

        .wrap {
            width: 100%;
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            padding: 1.3rem 0;
            padding-left: 1.5rem;

            @media (min-width: 768px) {
                flex-wrap: nowrap;
                padding: 2.3rem 0;
            }

            p {
                &:first-child {
                    color: white;
                    width: 100%;
                    font-size: 20px;
                    margin-bottom: 0.7rem;

                    @media (min-width: 768px) {
                        width: auto;
                        margin-right: 3rem;
                        margin-bottom: 0;
                        font-size: 28px;
                    }
                    @media (min-width: 1440px) {
                        margin-right: 5rem;
                        font-size: 36px;
                    }

                    span {
                        font-weight: lighter;
                    }
                }

                &:last-child {
                    color: rgba(128, 211, 142, 1);
                    font-weight: bolder;
                    font-size: 30px;
                    width: 100%;

                    @media (min-width: 768px) {
                        width: auto;
                        font-size: 41px;
                    }
                    @media (min-width: 1440px) {
                        font-size: 52px;
                    }
                }
            }
        }
    }

    .carousel--container {
        background-color: white;
        padding-top: 6rem;
        padding-bottom: 3rem;

        @media (min-width: 768px) {
            padding-top: 10rem;
        }

        .wrap {
            width: 100%;
            max-width: $defaultMaxWidth;
            margin: 0 auto;

            hgroup.title {
                text-align: center;
                margin-bottom: 3rem;

                @media (min-width: 768px) {
                    margin-bottom: 5rem;
                }

                h3 {
                    font-size: 20px;
                    font-weight: bold;

                    @media (min-width: 768px) {
                        font-size: 35px;
                    }
                }
            }

            .carousel--wrap {
                position: relative;

                .swiper-container {
                    width: 100%;
                    max-width: 1200px;
                    height: 400px;

                    @media (min-width: 768px) {
                        height: 500px;
                    }

                    .swiper-wrapper {
                        .swiper-slide {
                            display: flex;
                            justify-content: center;
                            padding: 0 1rem;
                            max-height: 333px;

                            @media (min-width: 768px) {
                                padding: 0;
                                max-height: 440px;
                            }

                            figure {
                                position: relative;
                                width: 100%;
                                overflow: hidden;
                                max-width: 257px;
                                max-height: 333px;

                                @media (min-width: 768px) {
                                    max-width: 340px;
                                    max-height: 440px;
                                }
                                @media (min-width: 1024px) {
                                    max-width: 300px;
                                    max-height: 440px;
                                }
                                @media (min-width: 1366px) {
                                    max-width: 340px;
                                    max-height: 440px;
                                }

                                img {
                                    object-fit: cover;
                                }

                                figcaption {
                                    position: absolute;
                                    bottom: 0;
                                    left: 0;
                                    width: 100%;
                                    height: 70px;
                                    display: flex;
                                    flex-direction: column;
                                    justify-content: center;
                                    background-color: rgba(255, 255, 255, 0.8);
                                    line-height: 1.8;
                                    text-align: center;

                                    @media (min-width: 768px) {
                                        height: 100px;
                                    }

                                    p {
                                        font-size: 15px;

                                        @media (min-width: 768px) {
                                            font-size: 18px;
                                        }

                                        &.price {
                                            font-weight: bolder;

                                            @media (min-width: 768px) {
                                                font-size: 16px;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }

                    .swiper-pagination {
                        bottom: 3%;

                        @media (min-width: 1440px) {
                            display: none;
                        }
                        .swiper-pagination-bullet {
                            opacity: 0.1;
                        }
                        .swiper-pagination-bullet-active {
                            opacity: 1;
                            background-color: dimgrey;
                        }
                    }
                }
                .carousel-button-next,
                .carousel-button-prev {
                    position: absolute;
                    top: 45%;
                    transform: translateY(-50%);
                    cursor: pointer;
                    opacity: 0.5;
                    display: none;

                    @media (min-width: 1440px) {
                        display: block;
                    }

                    img {
                        width: 50px;
                    }
                }

                .carousel-button-prev {
                    left: 3%;

                    @media (min-width: 1680px) {
                        left: 0;
                    }
                }

                .carousel-button-next {
                    right: 3%;

                    @media (min-width: 1680px) {
                        right: 0;
                    }
                }
            }
        }
    }

    .banner--bottom-container {
        overflow: hidden;
        background-color: rgba(62, 62, 62, 1);

        .wrap {
            position: relative;
            height: 300px;

            @media (min-width: 768px) {
                height: 350px;
            }

            .comments {
                position: absolute;
                top: 30%;
                left: 35%;
                transform: translate(-50%, -50%);
                color: white;

                @media (min-width: 768px) {
                    top: 50%;
                    left: 30%;
                }
                @media (min-width: 1920px) {
                    left: 35%;
                }

                p {
                    font-size: 18px;
                    line-height: 1.3;

                    @media (min-width: 768px) {
                        font-size: 35px;
                    }

                    span {
                        color: rgba(128, 211, 142, 1);
                        font-size: 26px;
                        font-weight: bolder;
                        padding-left: 1rem;

                        @media (min-width: 768px) {
                            font-size: 52px;
                        }
                    }
                }

                a.detail--view-btn {
                    @include flexCentered();
                    width: 100px;
                    height: 26px;
                    font-size: 13px;
                    border: 1px solid rgba(255, 255, 255, 0.5);
                    margin-top: 0.8rem;

                    @media (min-width: 768px) {
                        width: 160px;
                        height: 33px;
                        font-size: 17px;
                        margin-top: 1.5rem;
                    }
                }
            }

            img.delivery--truck {
                position: absolute;
                right: -35%;
                bottom: -45%;
                width: 330px;

                @media (min-width: 768px) {
                    width: 350px;
                    right: -10%;
                    bottom: -30%;
                }
                @media (min-width: 1024px) {
                    right: 5%;
                }
                @media (min-width: 1366px) {
                    right: 10%;
                }
                @media (min-width: 1440px) {
                    right: 15%;
                }
                @media (min-width: 1920px) {
                    right: 25%;
                }
                @media (min-width: 1920px) {
                    right: 25%;
                }
            }
        }
    }

    .banner--middle-container {
        background-color: white;
        padding-top: 4rem;

        @media (min-width: 768px) {
            padding-top: 10rem;
        }

        .wrap {
            width: 100%;
            max-width: $defaultMaxWidth;
            margin: auto;
            padding: 3rem 0;
            border-top: 1px solid rgba(0, 0, 0, 0.1);
            display: flex;
            flex-direction: column;
            align-items: center;

            @media (min-width: 768px) {
                padding: 6rem 0;
            }

            img {
                width: 80px;
            }

            p {
                line-height: 1.5;

                &.title {
                    font-size: 26px;
                    color: rgba(128, 211, 142, 1);
                }

                &.description {
                    font-size: 16px;
                }

                &.sub--description {
                    font-size: 13px;
                }
            }

            a.detail--view-btn {
                @include flexCentered();
                width: 100px;
                height: 26px;
                font-size: 13px;
                border: 1px solid rgba(0, 0, 0, 0.1);
                margin-top: 0.8rem;
            }

            @media (min-width: 768px) {
                img {
                    width: 100px;
                }

                p {
                    &.title {
                        font-size: 52px;
                    }

                    &.description {
                        font-size: 35px;
                    }

                    &.sub--description {
                        font-size: 18px;
                    }
                }

                a.detail--view-btn {
                    width: 160px;
                    height: 33px;
                    font-size: 17px;
                    margin-top: 1.5rem;
                }
            }
        }
    }
}
